import SectionMagazine10 from '@/components/SectionMagazine10'
import Trening from '@/components/trending'
import Latest from '@/components/latest'
import Weekly from '@/components/weekly'
import ButtonPrimary from '@/shared/ButtonPrimary'
import _ from 'lodash'
import { useRef, useEffect } from 'react'
import { useGetTrending } from '@/query/post/getTrendingByTime'
import { useGetLastPost } from '@/query/post/getLast'
import { useGetTopPost } from '@/query/post/getTop'
import LastCategoryHome from '@/components/lastCatHome'
import AdsSliderMulti from '@/components/adsSliderMulti'

export const Route = createFileRoute('/news/fr/')({
  component: App,

  head: () => ({
    meta: [
      {
        name: 'description',
        content:
          "Tous sur l'actualité en Algérie et à l’internationale, et toute l’information sur la politique, l’économie, les télécoms / TIC, le sport, la santé, le transport / automobiles. Fibladi news Tsa Algérie",
      },
      { title: 'Algérie actualité' },
      { property: 'og:locale', content: 'fr_FR' },
      { property: 'og:type', content: 'website' },
      {
        property: 'og:title',
        content: 'fibladi new : Toute l’actualité en Algérie',
      },
      {
        property: 'og:description',
        content:
          "Tous sur l'actualité en Algérie et à l’internationale, et toute l’information sur la politique, l’économie, les télécoms / TIC, le sport, la santé, le transport / automobiles. Fibladi news Tsa Algérie",
      },
      { property: 'og:url', content: 'https://fibladi.com/news/fr/' },
      { property: 'og:site_name', content: 'Fibladi fr' },
      {
        property: 'article:publisher',
        content: 'https://www.facebook.com/fibladicom',
      },
      {
        property: 'og:image',
        content:
          'https://fibladi.com/news/fr/wp-content/uploads/sites/2/2019/10/default-390x260-1.jpg',
      },
      { property: 'og:image:width', content: '390' },
      { property: 'og:image:height', content: '260' },
      { property: 'fb:app_id', content: '205286473167349' },
      {
        property: 'article:modified_time',
        content: '2025-07-04T20:10:47+00:00',
      },
      {
        property: 'twitter:card',
        content: 'summary',
      },
      {
        property: 'twitter:creator',
        content: '@fibladi',
      },
      {
        property: 'twitter:site',
        content: '@fibladi',
      },
      {
        property: 'twitter:title',
        content: 'Actualités Algérie',
      },
      {
        property: 'twitter:description',
        content:
          "Tout sur l'actualité en Algérie et dans le monde, et toutes les informations sur la politique, l'économie, les télécoms / TIC, le sport, la santé, le transport / automobiles. Fibladi news Tsa Algérie",
      },
      { title: 'Actualité Algérie' },
    ],
  }),
})

function App() {
  const sliderDev = useRef<HTMLDivElement>(
    null,
  ) as React.RefObject<HTMLDivElement>
  useGetLastPost
  const { data: dataTop, isLoading: isLoadingTop } = useGetTopPost('6220', '1')
  const { data: dataLast, isLoading: isLoadingLast } = useGetLastPost('fr', '1')

  const { data: dataweek, isLoading: isLoadingweek } = useGetTrending(
    '7j',
    '1',
    false,
    10,
  )
  const { data: dataday, isLoading: isLoadingday } = useGetTrending(
    '6h',
    '1',
    true,
    10,
  )

  const topPosts = dataTop?.posts ?? []
  const lastPosts = dataLast?.posts ?? []
  const weekPosts = dataweek?.posts ?? []
  const dayPosts = dataday?.posts ?? []

  useEffect(() => {
    // sliderDev.current.style.position = 'fixed'

    const handleScroll = _.throttle(() => {
      const side = sliderDev.current
      // console.log(' do window scrollY:', window.scrollY)
      // console.log(side.clientHeight - window.innerHeight)

      if (window.scrollY > side.clientHeight - window.innerHeight + 50) {
        // sliderlist.current.style.transform = `translateY(-${window.scrollY }px)`
        side.style.position = 'sticky'
        side.style.top = `-${side.clientHeight - window.innerHeight + 50}px`
      } else {
        // console.log('undo window scrollY:', window.scrollY)

        side.style.position = ''
        side.style.top = '0px'
      }
    }, 100)

    window.addEventListener('scroll', handleScroll)
    return () => window.removeEventListener('scroll', handleScroll)
  }, [])
  return (
    <div className="relative space-y-14">
      <div className="relative container mt-4 space-y-28 lg:mt-0 lg:space-y-32">
        <SectionMagazine10
          posts={topPosts}
          vr="fr"
          loading={isLoadingTop}
          isHome
        />
      </div>
      <div className="space-y-6">
        <div className="container space-y-28 lg:space-y-32">
          <div className="section-post-with-widgets relative">
            <div className="flex flex-col space-y-8 lg:flex-row ">
              <div className="w-full  -mt-5">
                <AdsSliderMulti />
                <Trening posts={dayPosts} vr="fr" loading={isLoadingday} />
                {isLoadingTop && (
                  <>
                    <div className="space-y-4">
                      <div className="bg-gray-300 h-7 w-1/2 rounded"></div>
                      <div className="grid grid-cols-1 sm:grid-cols-4 gap-8">
                        {[1, 2, 3, 4].map((_, index) => (
                          <div>
                            <div
                              key={index}
                              className="animate-pulse rounded-3xl overflow-hidden "
                            >
                              <div className="bg-gray-300 h-32 sm:h-44 w-full"></div>
                            </div>

                            <div className="py-4 space-y-2">
                              <div className="bg-gray-300 h-5 w-full rounded"></div>
                              <div className="bg-gray-300 h-5 w-5/6 rounded"></div>
                            </div>
                          </div>
                        ))}
                      </div>
                    </div>
                    <div className="space-y-4">
                      <div className="bg-gray-300 h-7 w-1/2 rounded"></div>
                      <div className="grid grid-cols-1 sm:grid-cols-4 gap-8">
                        {[1, 2, 3, 4].map((_, index) => (
                          <div>
                            <div
                              key={index}
                              className="animate-pulse rounded-3xl overflow-hidden "
                            >
                              <div className="bg-gray-300 h-32 sm:h-44 w-full"></div>
                            </div>

                            <div className="py-4 space-y-2">
                              <div className="bg-gray-300 h-5 w-full rounded"></div>
                              <div className="bg-gray-300 h-5 w-5/6 rounded"></div>
                            </div>
                          </div>
                        ))}
                      </div>
                    </div>
                  </>
                )}
                {!isLoadingTop && (
                  <>
                    <LastCategoryHome vr="fr" categorySlug="fibladiplus" />
                    <LastCategoryHome vr="fr" categorySlug="info" />
                    <LastCategoryHome vr="fr" categorySlug="economie" />
                    <LastCategoryHome vr="fr" categorySlug="telecoms-tic" />
                    <LastCategoryHome vr="fr" categorySlug="sport" />
                    <LastCategoryHome vr="fr" categorySlug="sante" />
                    <LastCategoryHome
                      vr="fr"
                      categorySlug="automobile-transport"
                    />
                  </>
                )}
              </div>
            </div>
          </div>
        </div>
        <div className="container space-y-28 lg:space-y-32">
          <div className="section-post-with-widgets relative">
            <div className="flex flex-col space-y-8 lg:flex-row ">
              <div className="w-full lg:w-3/5 xl:w-2/3 xl:pe-14 -mt-4">
                {isLoadingLast && (
                  <div className="my-10 h-7 w-1/2 bg-gray-300"></div>
                )}
                <Latest
                  loading={isLoadingLast}
                  posts={lastPosts}
                  heading="Dernières News"
                  vr="fr"
                />
                <div className="mt-8 flex justify-center cursor-pointer">
                  <Link
                    to="/news/fr/$postId/page/$pagenumber"
                    params={{ postId: 'dernieres-actualites', pagenumber: '2' }}
                    className="flex items-center space-x-4 w-full"
                  >
                    <ButtonPrimary className="cursor-pointer w-full">
                      Plus de News
                      {/*<ArrowRightIcon className="size-4" />*/}
                    </ButtonPrimary>
                  </Link>
                </div>
              </div>
              <div className="w-full lg:w-2/5 xl:w-1/3 mb-8">
                <div className="space-y-7 " ref={sliderDev}>
                  <Weekly
                    loading={isLoadingweek}
                    posts={weekPosts}
                    full
                    vr="fr"
                    hedding={
                      // ? 'الأكثر رواجًا هذا الاسبوع'
                      'Les plus populaires cette semaine'
                    }
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
